<template>
  <v-layout>
    <v-card>
      <span slot="header">
        用户登录
      </span>
      <div slot="body">
        <el-form :model="userForm" :rules="rules" ref="userForm">
          <el-form-item prop="username">
            <el-input v-model="userForm.username" placeholder="输入手机号">
              <template slot="prepend"><i class="el-icon-fa-mobile" style="font-size: 20px;width:14px;text-align:center"></i></template>
            </el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input v-model="userForm.password" placeholder="输入密码" type="password">
              <template slot="prepend"><i class="el-icon-fa-key" style="text-align:center"></i></template>
            </el-input>
          </el-form-item>
          <button @click.prevent="login()" class="btn btn-blue btn-block" style="margin: 20px 0;line-height: 36px"><i class="el-icon-fa-sign-in"></i> 登录系统</button>
        </el-form>
      </div>
      <el-row slot="footer">
        <!-- <hr class="hr-text" data-content="OR"> -->
        <el-col :span="12">
          <el-button type="green" icon="fa-user-plus" @click.prevent="gotoRegister">注册账户</el-button>
        </el-col>
        <el-col :span="12" style="text-align:right;line-height: 30px">
          忘记密码?
          <router-link :to="{ name: 'register' }">找回密码</router-link>
        </el-col>
      </el-row>
    </v-card>
  </v-layout>
</template>

<script>
  import {Form, FormItem, Input, Row, Col, Button} from 'element-ui'
  import VLayout from '@/pages/layout/Minimal'
  import VCard from '@/components/Card'

  export default {
    data () {
      var validateMobile = (rule, val, callback) => {
        if (!(/^1[23456789]\d{9}$/.test(val))) {
          callback(new Error('手机号码格式不正确'))
        }
        callback()
      }
      return {
        userForm: {
          username: null,
          password: null
        },
        rules: {
          username: [
            {validator: validateMobile, trigger: 'blur'}
          ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      login () {
        this.$refs['userForm'].validate((valid) => {
          if (valid) {
            this.$store.dispatch('auth/login', this.userForm)
          }
        })
      },
      gotoRegister () {
        this.$router.push('register')
      }
    },
    components: {
      VLayout,
      VCard,
      'el-form': Form,
      'el-form-item': FormItem,
      'el-input': Input,
      'el-col': Col,
      'el-row': Row,
      'el-button': Button
    }
  }
</script>



