<template>
  <div class="card">
    <el-row class="header">
      <el-col :span="24">
        <div class="title">
          <slot name="header"></slot>
        </div>
      </el-col>
    </el-row>
    <div
      class="card-body"
      v-if="hasSlot('body')"
    >
      <slot name="body"></slot>
    </div>
    <div
      class="card-footer"
      v-if="hasSlot('footer')"
    >
      <slot name="footer"></slot>
    </div>
  </div>
</template>

<script>
  import SlotMixin from '@/mixins/slot'
  import {Row, Col} from 'element-ui'
  export default {
    name: 'card',
    mixins: [
      SlotMixin
    ],
    props: {
      contextualStyle: {
        type: String,
        required: false
      }
    },
    computed: {
      classNamesHeader () {
        const classNames = ['card-header']

        if (this.contextualStyle) {
          classNames.push(`bg-${this.contextualStyle}`)
          classNames.push('text-white')
        } else {
          classNames.push('bg-default')
        }

        return classNames
      }
    },
    components: {
      'el-row': Row,
      'el-col': Col
    }
  }
</script>
<style scoped>
  .header .title {
    margin-top: 15px;
  }
  .card-footer {
    /*margin-top: 30px;*/
    margin-bottom: 20px;
  }
</style>



